import React, { Component } from 'react';
import moment from 'moment';


const getDescription = (event) => {
  switch (event.type) {
    case 'PERSON_ENTERED':
      return 'Har ankommet Paradise.';
    case 'PERSON_LEFT':
      return 'Har forlatt Paradise.';
    default:
      return 'Mangler beskrivelse';
  }
};

class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      members: [],
      events: []
    };

    this.fetchMembers = this.fetchMembers.bind(this);
    this.fetchEvents = this.fetchEvents.bind(this);
    this.fetchAll = this.fetchAll.bind(this);
  }

  componentDidMount() {
    this.fetchAll();
    setInterval(this.fetchAll, 3000);
  }

  fetchAll() {
    this.fetchMembers();
    this.fetchEvents();
  }

  fetchMembers() {
    fetch('https://paradise-backend.herokuapp.com/api/members')
    .then(response => response.json())
    .then(members => this.setState({ members }));
  }

  fetchEvents() {
    fetch('https://paradise-backend.herokuapp.com/api/bluetooth-events')
    .then(response => response.json())
    .then(events => events.map(
      event => Object.assign(event, {
        description: getDescription(event),
        date: moment(event.date)
      })
    ))
    .then(events => this.setState({ events }));
  }

  render() {
    return (
      <div className="wrapper">
        <header className="main-header">
          <div className="logo">Paradise Fantasy</div>
          <nav className="navbar" />
        </header>

        <aside className="main-sidebar">
          <section className="sidebar">
            <ul className="sidebar-menu">
              <li className="header">HEADER</li>
              <li>
                <a href="/">
                  <i className="fa fa-link" />
                  <span>Link?</span>
                </a>
              </li>
            </ul>
          </section>
        </aside>

        <div className="content-wrapper" style={{ minHeight: 'calc(100vh - 101px)' }}>
          <section className="content-header">
            <h1>
              Dashboard
              <small>Version 0.1</small>
            </h1>
          </section>

          <section className="content">
            <div className="row">
              <div className="col-md-3 col-sm-6 col-xs-12">
                <div className="info-box">
                  <span className="info-box-icon bg-aqua">
                    <i className="ion ion-gear-a" />
                  </span>
                  <div className="info-box-content">
                    <span className="info-box-text">CPU Traffic</span>
                    <span className="info-box-number">90<small>%</small></span>
                  </div>
                </div>
              </div>

              <div className="col-md-3 col-sm-6 col-xs-12">
                <div className="info-box">
                  <span className="info-box-icon bg-red">
                    <i className="fa fa-google-plus" />
                  </span>
                  <div className="info-box-content">
                    <span className="info-box-text">LIKES</span>
                    <span className="info-box-number">41,110</span>
                  </div>
                </div>
              </div>

              <div className="col-md-3 col-sm-6 col-xs-12">
                <div className="info-box">
                  <span className="info-box-icon bg-green">
                    <i className="ion ion-ios-cart-outline" />
                  </span>
                  <div className="info-box-content">
                    <span className="info-box-text">SALES</span>
                    <span className="info-box-number">760</span>
                  </div>
                </div>
              </div>

              <div className="col-md-3 col-sm-6 col-xs-12">
                <div className="info-box">
                  <span className="info-box-icon bg-yellow">
                    <i className="ion ion-ios-people-outline" />
                  </span>
                  <div className="info-box-content">
                    <span className="info-box-text">MEDLEMMER</span>
                    <span className="info-box-number">{this.state.members.length}</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="row">
              <div className="col-md-6 col-sm-12">
                <div className="box box-info">
                  <div className="box-header with-border">
                    <h3 className="box-title">Siste hendelser</h3>
                  </div>
                  <div className="box-body">
                    <div className="table-responsive">
                      <table className="table no-margin">
                        <thead>
                          <tr>
                            <th>Tidspunkt</th>
                            <th>Hvem</th>
                            <th>Hva</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.events.map(event => (
                              <tr key={event.id}>
                                <td>{event.date.format('DD/MM HH:mm:ss')}</td>
                                <td>{event.subject}</td>
                                <td>{event.description}</td>
                              </tr>
                            ))
                          }
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className="box-footer clearfix" />
                </div>
              </div>

              <div className="col-md-6 col-sm-12">
                <div className="box box-info">
                  <div className="box-header with-border">
                    <h3 className="box-title">Medlemmer</h3>
                  </div>
                  <div className="box-body">
                    <div className="table-responsive">
                      <table className="table no-margin">
                        <thead>
                          <tr>
                            <th>Navn</th>
                            <th>MAC addresse</th>
                            <th>Farge</th>
                            <th>Rom</th>
                          </tr>
                        </thead>
                        <tbody>
                          {
                            this.state.members.map(member => (
                              <tr key={member.id}>
                                <td>{member.name}</td>
                                <td>{member.bluetooth_address}</td>
                                <td>{member.color}</td>
                                <td>{member.room}</td>
                              </tr>
                            ))
                          }
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div className="box-footer clearfix" />
                </div>
              </div>
            </div>
          </section>
        </div>

        <footer className="main-footer">
          <strong>Paradise Fantasy</strong>
        </footer>
      </div>
    );
  }
}

export default Dashboard;



